<template>
  <div >
            <el-card :body-style="{ padding: '0px' }">
            <img :src="computedImage(item)" class="image img-responsive" lazy>
            <div style="padding: 14px;">
                <span>{{item.姓名}}</span>
                <div class="bottom clearfix">
                <span class="text-capitalize">{{item.身份}}</span>
                <el-button type="text" class="button" @click="jumpTo(item)">Learn More</el-button>
                </div>
            </div>
            </el-card>
  </div>
</template>

<script >
import logo from '../../static/piclogo/默认头像 .svg'
export default{
    props:{
      item :{
      type : Object,
      }
    },
    data(){
      return{
        loadError: false,
      }
    },
    computed:{
      
    },
    methods:{
      computedImage(item) {
        if (item.photo == 1) {
          return item.logo;
        } else {
          return '../../static/piclogo/默认头像 .svg';
        }
    },
    jumpTo(e){
        this.$router.push({path:'/people/peopleDetail/'+ e.name, query:e});
    },
    }
}
</script>
<style scoped>
 .time {
    font-size: 13px;
    color: #999;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }

</style>


