<template>
  <div>
    <div class="banner container-fuild text-center">PEOPLE </div>
    <bread></bread>
    <div class="container">
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-3" id="myScrollspy">
          <ul class="nav nav-tabs nav-stacked center-block" id="myNav">
            <p>Research Interests</p>
            <li
              :class="item.id==id?'active':''"
              v-for="(item,index) in serviceNavList"
              :key="index"
            >
              <a :href="'#'+item.id">{{item.title}}</a>
            </li>
          </ul>
        </div>
        <div class="col-xs-12 col-sm-12 col-md-9 content   wow  slideInUp">
          <div class="content-block" v-for="(it,index) in serviceContentList" :key="index">
            <h2 :id="it.id">
              {{it.title}}
              <small>/ {{it.eng_title}}</small>
            </h2>
            <div class="pic">  
              <picbox v-for="item in filtersection(it.title)" :key="item.序号" :item="item"></picbox>
            </div>
            
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { WOW } from "wowjs";
import picbox from '../components/PicBox.vue';
import peopledata from '../assets/data/peopledata.json';
export default {
  name: "ServiceDetail",
  components:{
    picbox
  },
  data() {
    return {
      id: "section-1",
      serviceNavList: [
        {
          id: "section-1",
          title: "Remote sensing inversion"
        },
        {
          id: "section-2",
          title: "Quantification of emissions"
        },
        {
          id: "section-3",
          title: "Impact attributio"
        },
      ],
      serviceContentList: [
         {
          id: "section-0",
          number:0,
          title: "LEADER",
          eng_title: "",
        },
        {
          id: "section-1",
          number:1,
          title: "遥感反演",
          eng_title: "Remote sensing inversion",
        },
        {
          id: "section-2",
          number:2,
          title: "排放量化",
          eng_title: "Quantification of emissions",
        },
        {
          id: "section-3",
          number:3,
          title: "影响归因",
          eng_title: "Impact attribution",
        },
      ],
      people:{},
      section1:{},
      section2:{},
      section3:{},
    };
  },
  methods:{
   filtersection(e){
      this['section' + e.number] = this.people.filter(item=> item.section == e)
      .sort((a, b) => a.number - b.number);//对人员的研究方向分类并按number大小进行排序
      return this['section' + e.number] ;
    }
  },
  mounted() {
    this.people = peopledata;
    this.id = this.$route.params.id;
    var top = document.getElementById(this.id).offsetTop;
    $(window).scrollTop(top + 300);
    $("#myNav").affix({
      offset: {
        top: 300
      }
    });
    var wow = new WOW();
    wow.init();
  }
};
</script>
<style scoped>
.banner {
  color: #fff;
  font-size: 30px;
  height: 150px;
  line-height: 150px;
  background-image: url("../assets/img/banner_2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll;
  background-position: center center;
}
ul.nav-tabs {
  width: 200px;
  margin-top: 40px;
  border-radius: 4px;
  background: #fff;
  z-index: 99999;
  border: 1px solid #474747;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.067);
}
ul.nav-tabs li {
  text-align: center;
  margin: 0;
  border-top: 1px solid #474747;
}
ul.nav-tabs p {
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  background: #474747;
  margin: 0;
  padding: 10px 0;
}
ul.nav-tabs li:first-child {
  border-top: none;
}
ul.nav-tabs li a {
  margin: 0;
  padding: 8px 16px;
  border-radius: 0;
}
ul.nav-tabs li.active a,
ul.nav-tabs li.active a:hover {
  color: #fff;
  background: #474747;
  border: 1px solid #474747;
}
ul.nav-tabs li:first-child a {
  border-radius: 4px 4px 0 0;
}
ul.nav-tabs li:last-child a {
  border-radius: 0 0 4px 4px;
}
ul.nav-tabs.affix {
  top: 30px;
}
.content-block {
  margin: 50px 0;
}
.content-block > h2 {
  padding: 20px 0;
  border-bottom: 1px solid #ccc;
}
.pic {
  display: grid;
  grid-template-columns:repeat(auto-fill, minmax(180px, 1fr));
  justify-self: center;
  align-self: center;
  gap: 20px;
  filter:border-radius(50px) ;
}

</style>



